.region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents {
  width: auto;
  max-width: 500px;
  border-top: 1px solid #dee2e6;
}

@media (max-width: 991.98px) {
  .region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents {
    position: static;
    border-left: 0;
    border-right: 0;
  }
}

@media (min-width: 992px) {
  .region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents {
    min-width: 300px;
    margin-top: 15px;
    top: 100%;
    right: 0;
    border: 1px solid #dee2e6;
    border-radius: 1px;
    box-shadow: 0 0 11px -7px #a4afba;
  }
}

.region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents__inner {
  padding: 10px 20px;
}

.region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents__items {
  width: 100%;
  display: table;
}

.region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents .cart-block--contents__links {
  margin-bottom: -0.25rem;
  padding-bottom: 10px;
}

.region-secondary-menu .block-commerce-cart .cart--cart-block .cart-block--contents .cart-block--contents__links a {
  color: #fff;
}

.view-d-commerce-cart-form .views-field-purchased-entity {
  width: 50%;
  min-width: 250px;
}

.view-d-commerce-cart-form .views-field-edit-quantity {
  width: 10%;
}

.view-d-commerce-cart-form .views-field-edit-quantity fieldset {
  margin: 0;
}

.view-d-commerce-cart-form .views-field-remove-button {
  text-align: center;
}

.view-d-commerce-cart-form .views-field-remove-button input {
  margin: 0;
}

.view-d-commerce-cart-form td.views-field {
  vertical-align: middle;
}

.order-total-line {
  padding: 0.25rem 0;
}

.order-total-line-label {
  margin-right: 0.5rem;
}

.order-total-line-value {
  width: auto;
}

.view-d-commerce-user-orders .views-field-order-number {
  width: 15%;
}

.view-d-commerce-user-orders td.views-field {
  text-align: center;
}

.checkout-pane-login {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 2rem -20px;
}

@media (min-width: 992px) {
  .checkout-pane-login {
    flex-wrap: nowrap;
  }
}

.address-book__container .address-book__profile {
  flex-basis: content;
}

.easypack-widget .btn-search {
  margin-top: 0;
}

.d-commerce-checkout-register-container {
  border: 1px solid #dee2e6;
}

.d-commerce-checkout-register-container input,
.d-commerce-checkout-register-container .image-widget-data,
.d-commerce-checkout-register-container .confirm-parent, .d-commerce-checkout-register-container .password-parent {
  width: 100%;
  max-width: 100%;
}
